:root {
	--grad-one: rgba(3, 56, 244, 1);
	--grad-two: rgba(224, 37, 206, 1);
}

@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic);

* {
	box-sizing: border-box;
}

html {
	height: 100vh;
	min-width: 100vw;
	min-height: 100%;
}

body {
	background: url('../pages/back.png');
	background-size: cover;
	background-position: 100%;
	background-repeat: no-repeat;
	height: 100vh;
	min-width: 961px;
	min-height: 100%;
	margin: 0;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
}

.description {
	background-color: #292929;
	color: grey;
	font-family: 'IBM Plex Mono', monospace;
	font-size: 14px;
	font-weight: 500;
	width: 200px;
	padding: 5px;
	border-radius: 5px;
	position: absolute;
	border: 2px solid black;
	top: 0;
	left: 0;
	transition: all .3s ease;
	&__button {
		position: absolute;
		width: 100px;
		height: 20px;
		background-color: grey;
		color: black;
		bottom: -22px;
		right: 0;
		border-radius: 2px;
		text-align: center;
		cursor: pointer;
	}
}

.hidden {
	height: 0px;
	font-size: 0px;
}
.hidden > .description__button {
	font-size: 14px;
}

.description ul {
	list-style: square;
	padding: 0 0 0 20px;
	margin: 0;
}

.description ul > li{
}

.text-area__wrapper {
}

.text-area {
	white-space: pre-wrap;
	width: 961px;
	height: 50vh;
	font-family: 'IBM Plex Mono', monospace;
	font-size: 18px;
	font-weight: 500;
	color: whitesmoke;
	resize: none;
	overflow: auto;
	padding: 10px;
	border-radius: 10px;
	outline: 5px solid black;
	background-color: #292929;
	border: none;
}



.keyboard__wrapper {
	display: flex;
	flex-wrap: wrap;
	column-gap: 14px;
	row-gap: 10px;
	justify-content: space-between;
	padding: 20px 10px 24px;
	border-radius: 10px;
	background: linear-gradient(90deg, var(--grad-one) 0%, var(--grad-two) 93.75%) no-repeat;
	max-width: 1100px;
/* 	min-width: 952px;
	width: 1100px; */
	transform: perspective(400px) rotateX(21deg);
	outline: 20px solid black;
}
.key {
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 10px;
	flex: 1 1 5%;
	height: 5vh;
	color: rgb(0, 195, 255);
	background-color: rgb(29, 28, 28);
	font-weight: 700;
	box-shadow: 0 5px 0 0 grey;
	transform: translate(0);
	transition: all .1s ease;
}

.key:hover {
	text-shadow: rgb(0, 26, 255) 0 0 5px, rgb(0, 26, 255) 0 0 5px, rgb(0, 195, 255) 0 0 10px, rgb(0, 195, 255) 0 0 10px, rgb(0, 195, 255) 0 0 10px, rgb(0, 195, 255) 0 0 10px;
	cursor: pointer;
}

.key:active {
	transform: translate(0, 2px);
	box-shadow: 0 0px 0 0 grey;
	background-color: rgba(29, 28, 28, 0.5);
}
._active {
	transform: translate(0, 2px);
	box-shadow: 0 0px 0 0 grey;
	background-color: rgba(29, 28, 28, 0.5);
}

.Backspace {
	flex: 1 1 14.2%;
}

.Tab, .Delete {
	flex: 1 1 6.8%;
}

.CapsLock, .Enter, .ShiftLeft, .ShiftRight {
	flex: 1 1 13%;
}

.Space {
	flex: 1 1 43.5%;
}

.ControlLeft, .ControlRight{
	flex: 1 1 6.5%;
}


